<template>
   <div id="box">
        <h1></h1>
        <hr/>
        <h6>
           1. 2000  
        </h6>
        <div class="char" ref="k1"></div>
        <h6>
            2.    =-
        </h6>
        <div class="char" ref="k2"></div>
        <h6>
           3.  
        </h6>
        <div class="char" ref="k3"></div>
        <h6>
           4. 2000 20    
        </h6>
        <div class="char" ref="k4"></div>
        <h6>
          5.   open high low close yolume 
        </h6>
        <div class="char" ref="k5"></div>
    </div>
</template>

<script setup>
import {onMounted, ref} from 'vue'
const echarts = require("echarts")
const axios = require("axios")

let data = []
const k1 = ref("k1")
const k2 = ref("k2")
const k3 = ref("k3")
const k4 = ref("k4")
const k5 = ref("k5")

onMounted(async() => {
    await axios.get(window.BaseURL+"/Gold").then(res => data = res.data)
    data = data.map(raw => {
        return {
            "": raw["Close"],
            "": raw["Date"],
            "": Number(raw["High"]),
            "": Number(raw["Low"]),
            "": raw["Open"],
            "": raw["Volume"],
            "": raw["Date"].split("-")[0],
            "": raw["Date"].split("-")[1],
        }
    })

    let years = [...new Set(data.map(raw=>raw[""]))]
    let months = [...new Set(data.map(raw=>raw[""]))]

    let plot1 = echarts.init(k1.value)
    let opt1 = {
        xAxis: {type: "time"},
        yAxis: {},
        series: [
            {
                type: "line",
                name: "",
                symbol: 'none',
                data: data.map(raw=>[ new Date(raw.).getTime(), raw.])
            },
            {
                type: "line",
                name: "",
                symbol: "none",
                data: data.map(raw=>[ new Date(raw.).getTime(), raw.])
            }
        ],
        legend: {
            data: ["", ""]
        },
        dataZoom: [ { }, { } ],
    }
    plot1.setOption(opt1)

    let plot2 = echarts.init(k2.value)
    let opt2 = {
        xAxis: {type: "time"},
        yAxis: {},
        series: [
            {
                type: "line",
                symbol: "none",
                data: data.map(raw => {
                    return  [new Date(raw.).getTime(), raw.-raw.]
                })
            }
        ],
        dataZoom: [{},{}]
    }
    plot2.setOption(opt2)

    let plot3 = echarts.init(k3.value)
    let opt3 = {
        xAxis: {data: years},
        yAxis: {},
        legend: {
            data: months
        },
        series: 
            months.map(m=>{
                return {
                    type: "bar",
                    name: m,
                    stack: "0",
                    data: years.map(y=> {
                        let sum = 0
                        data.filter(raw => raw.==y && raw.==m).forEach(el => {
                            sum += Number(el.)
                        })
                        return sum
                    })
                }
            })
        
    }
    console.log(opt3)
    plot3.setOption(opt3)

    let max = {index:0, value: data[0].}
    let min = {index:0, value: data[1].}
    data.forEach((el, index) => {
        if(el.<min.value){
            min.index = index
            min.value = el.
            min.room = el.
        }
        if(el.>min.value){
            max.index = index
            max.value = el.
        }
    })
    console.log(min, max)
    let minToMax = data.slice(min.index, max.index + 1)

    let plot4 = echarts.init(k4.value)
    let opt4 = {
        xAxis: {type: "time"},
        yAxis: {},
        series: [
            {
                type: "line",
                symbol: "none",
                data: minToMax.map((raw, index) => {
                    if(index != minToMax.length){
                        return [new Date(raw.).getTime(), (raw. - min.value) * min.room]
                    }else{
                        return [new Date(raw.).getTime(), (max.value - min.value) * min.room]
                    }
                    
                })
            }
        ],
        dataZoom: [{}, {}]
    }
    plot4.setOption(opt4)



    let last_data=data[data.length -1]
    
    let indicator = [
    "",
    "",
    "",
    "",
    ""]
    let indicator_5 = indicator.map(o=>{
        return {name: o}
    })
    
    let legend=[""]


    let plot5 = echarts.init(k5.value)
    let opt5 = {
        title:{
            text:""
        },
        legend:{data:legend},
        radar:{
            indicator: indicator_5
        },
        series:[{
            type:'radar',
            data:[
                {name : "", value:indicator.map(o=>{
                    return Number(last_data[o])
                }) }
            ]
        }]
    }
    console.log(opt5)
    plot5.setOption(opt5)

})



</script>


<style scoped>
#box{
    display: flex;
    flex-direction: column;
    align-items: center;
}
h6{
    width: 800px;
}
.char{
    width: 800px;
    height: 600px;
    border: 1px solid rgb(148, 216, 232);
}
</style>